$divide-line: 1px solid #f3f3f3;

$fe: 'fade-enter';
$fl: 'fade-leave';
.#{$fe},
.#{$fl}-to {
  opacity: 0;
}

.#{$fe}-to,
.#{$fl} {
  opacity: 1;
}

.#{$fe}-active,
.#{$fl}-active {
  transition: all 0.2s ease-in-out;
}

@mixin padding {
  padding: {
    left: 24px;
    right: 24px
  }
  @media only screen and (max-width: 959px) {
    padding: {
      left: 16px;
      right: 16px
    }
  }
  @media only screen and(min-width: 960px) {
    max-width: 900px;
  }
  @media only screen and (min-width: 1264px) {
    max-width: 1185px;
  }
  @media only screen and (min-width: 1904px) {
    max-width: 1785px;
  }
}




































































































.home-movie-list-pane {
    color: #333;
    &:not(:last-child) {
        border-bottom: $divide-line;
    }
    .list-title {
        * {
            font-size: 1.2em
        }
        display: flex;
        position: relative;
        padding: {
            top: 0.5em;
            bottom: 0.5em;
        }
        //@include padding;
        @media only screen and (max-width: 950px) {
            padding: {
                left: 16px;
                right: 16px;
            }
        }
        span {
        }
        .more {
            margin-left: auto;
            color: #2aac5e;
            text-decoration: none;
        }
        /*&::before {*/
        /*position: absolute;*/
        /*display: block;*/
        /*left: 0;*/
        /*content: "";*/
        /*width: 6.67px;*/
        /*height: 100%;*/
        /*background-color: #2aac5e;*/
        /*}*/
    }

    .movie-list {
        $width: 100px;
        height: $width*1.43 + 42px;
        .movie-item {
            width: $width;
            margin-right: 2vmin;
            &:first-child {
                margin-left: 16px;
            }
            &:last-child {
                margin-right: 16px;
            }
        }
    }

}
