@charset "UTF-8";
.fade-enter,
.fade-leave-to {
  opacity: 0; }

.fade-enter-to,
.fade-leave {
  opacity: 1; }

.fade-enter-active,
.fade-leave-active {
  transition: all 0.2s ease-in-out; }

* {
  padding: 0;
  margin: 0;
  font-size: 12px;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif; }

html, body {
  color: #333333; }

p {
  margin: 0; }

ul {
  padding: 0; }
