$divide-line: 1px solid #f3f3f3;

$fe: 'fade-enter';
$fl: 'fade-leave';
.#{$fe},
.#{$fl}-to {
  opacity: 0;
}

.#{$fe}-to,
.#{$fl} {
  opacity: 1;
}

.#{$fe}-active,
.#{$fl}-active {
  transition: all 0.2s ease-in-out;
}

@mixin padding {
  padding: {
    left: 24px;
    right: 24px
  }
  @media only screen and (max-width: 959px) {
    padding: {
      left: 16px;
      right: 16px
    }
  }
  @media only screen and(min-width: 960px) {
    max-width: 900px;
  }
  @media only screen and (min-width: 1264px) {
    max-width: 1185px;
  }
  @media only screen and (min-width: 1904px) {
    max-width: 1785px;
  }
}









































































.movie-item {
  display: block;
  width: 100%;
  a {
    display: inline-block;
    color: #333;
    text-decoration: none;
    width: 100%;
    .img-wp {
      position: relative;
      overflow: hidden;
      &::before {
        content: "";
        display: block;
        padding-top: 143%;
      }
      img {
        display: block;
        width: 100%;
        min-height: 100%;
        position: absolute;
        top: -100%;
        left: -100%;
        right: -100%;
        bottom: -100%;
        margin: auto;
        transition: filter 0.5s ease-in-out;
        &.slide-img {
          filter: opacity(0);

          &[lazy="loaded"] {
            filter: opacity(100%);
          }
          &.update {
            filter: opacity(0);
          }
        }
      }
    }
    .movie-info {
      height: 42px;
      p {
        padding-top: 2px;
        font-size: 1.2em;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        text-align: left;
        span {
          vertical-align: middle;
        }
        .rating-star {
          display: inline-block;
          width: 5rem;
          height: 1rem;
          background: {
            image: url("../assets/sprite/sprite-raing.png");
            repeat: no-repeat;
            size: cover;
          }
        }
        &.rating {
          font-weight: normal;
          font-size: 1em;
          color: gray;
          text-align: left;
        }
      }
    }
  }
}
